<template>
<div style="padding-top: 10px">
  <msg-block v-for="(msg, i) in msgBlockInfo" :key="i" :bg-title="msg.title" sm-title1="开始日期" :content1="msg.time" sm-title2="地点" :content2="msg.zone"></msg-block>
</div>
</template>

<script>
import MsgBlock from '../Utils/MsgBlock'
export default {
  name: 'Zone',
  components: {
    msgBlock: MsgBlock
  },
  data: function () {
    return {
      msgBlockInfo: [
        {
          id: 1,
          title: '宝钢湛江钢铁三高炉系统',
          time: '2018-12-21',
          zone: '广东 湛江'
        }
      ]
    }
  },
  created: function () {
    this.$emit('setBottomBar', true)
    this.$emit('setReturnBtn', false)
    this.$emit('setMenuBtn', false)
    this.$emit('setTitle', '工地切换')
  }
}
</script>

<style scoped>

</style>



