<template>
  <div class="bottom-bar fixed-bottom">
    <div class="bar-item" :class="isIndexActive ? 'active' : ''" @click="click('Index')">
      <div class="item-content">
        <i class="fa fa-home"></i>
        <span>首页</span>
      </div>
    </div>
    <div class="bar-item" :class="isWFReadActive ? 'active' : ''" @click="click('WFRead')">
      <div class="item-content">
        <i class="fa fa-file-alt"></i>
        <span>待阅</span>
      </div>
    </div>
    <div class="bar-item" :class="isWFDoActive ? 'active' : ''" @click="click('WFDo')">
      <div class="item-content">
        <i class="fa fa-tasks"></i>
        <span>待办</span>
      </div>
    </div>
    <div class="bar-item" :class="isSiteActive ? 'active' : ''" @click="click('Zone')">
      <div class="item-content">
        <i class="fa fa-map-marker-alt"></i>
        <span>工地</span>
      </div>
    </div>
    <div class="bar-item" :class="isSettingActive ? 'active' : ''" @click="click('Setting')">
      <div class="item-content">
        <i class="fa fa-cog"></i>
        <span>设置</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AppBottomBar',
  data: function () {
    return {
    }
  },
  methods: {
    click: function (routeName) {
      this.$router.push({name: routeName})
    }
  },
  computed: {
    isIndexActive: function () {
      return this.$route.name === 'Index'
    },
    isWFReadActive: function () {
      return this.$route.name === 'WFRead'
    },
    isWFDoActive: function () {
      return this.$route.name === 'WFDo'
    },
    isSiteActive: function () {
      return this.$route.name === 'Zone'
    },
    isSettingActive: function () {
      return this.$route.name === 'Setting'
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../assets/scss/config";
  .bottom-bar {
    padding-top: 10px;
    padding-bottom: 5px;
    max-width: $max-width;
    margin: 0 auto;
    background-color: $gray-300;
    border-top: $gray-400 solid 1px;
    .bar-item {
      display: inline-block;
      float: left;
      width: calc(20%);
      .item-content {
        text-align: center;
        .fa {
          display: block;
          font-size: 30px;
        }
        span {
          font-size: 7px;
        }
      }
      color: $gray-750;
      &.active {
        color: $cyan;
      }
      cursor: pointer;
    }
    clear: both;
  }
</style>



