<template>
<div class="appContainer">
<app-header :enableReturnBtn="enableReturnBtn" :enableMenuBtn="enableMenuBtn" :title="title"></app-header>
<div id="app-content" :style="enableBottomBar ? {marginBottom: '88px'} : {marginBottom: '20px'}">
  <transition name="page">
    <router-view @setBottomBar="setBottomBar" @setReturnBtn="setReturnBtn" @setMenuBtn="setMenuBtn" @setTitle="setTitle"></router-view>
  </transition>
  <div id="footer">
    <alert ref="alert"></alert>
    <span @click="alert('Develop by Tonychanczm', null, '点个赞', '反手再点个赞')">
      /* ———— 没错，这里已经没了 ———— */
    </span>
  </div>
</div>
<transition name="bottombar">
  <app-bottom-bar v-show="enableBottomBar"></app-bottom-bar>
</transition>
</div>
</template>

<script>
import AppHeader from './AppHeader'
import AppBottomBar from './AppBottomBar'
import AppContent from './AppContent'
import Alert from './Utils/Alert'
export default {
  name: 'AppLayout',
  components: {
    appHeader: AppHeader,
    appBottomBar: AppBottomBar,
    appContent: AppContent,
    alert: Alert
  },
  data: function () {
    return {
      enableBottomBar: false,
      enableReturnBtn: false,
      enableMenuBtn: false,
      title: 'Loading',
      zoneName: '宝钢湛江钢铁三高炉系统'
    }
  },
  methods: {
    setBottomBar: function (flag = true) {
      this.enableBottomBar = flag
    },
    setReturnBtn: function (flag = true) {
      this.enableReturnBtn = flag
    },
    setMenuBtn: function (flag = true) {
      this.enableMenuBtn = flag
    },
    setTitle: function (title) {
      document.getElementById('doctitle').innerHTML = title
      this.title = title
    },
    alert: function (title, confirmCallback = null, confirmText = null, cancelText = null, cancelCallback = null) {
      this.$refs.alert.show(title, confirmCallback, confirmText, cancelText, cancelCallback)
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../assets/scss/config";
  .appContainer {
    background-color: $gray-200;
  }
  #app-content {
    margin-top: 56px;
    transition: margin-bottom .6s ease;
  }
  #footer {
    margin-top: 20px;
    color: $gray-400;
    text-align: center;
    font-size: 10px;
  }

  .bottombar-enter-active, .bottombar-leave-active {
    transition: all .6s ease;
  }
  .bottombar-enter, .bottombar-leave-to {
    transform: translateY(65px);
  }
</style>



