<template>
  <div class="page">
    <carouserl></carouserl>
    <div class="index-menu row">
      <div class="menu-item col-4" @click="$router.push('/video/live')">
        <i class="fa fa-camera icon1"></i>
        <span>视频监控</span>
      </div>
      <div class="menu-item col-4">
        <i class="fa fa-chart-bar icon2"></i>
        <span>视频统计</span>
      </div>
      <div class="menu-item col-4" @click="$router.push('/env/status')">
        <i class="fa fa-seedling icon3"></i>
        <span>环境监测</span>
      </div>
      <div class="menu-item col-4" @click="$router.push('/device/status')">
        <i class="fa fa-tram icon4"></i>
        <span>设备监测</span>
      </div>
      <div class="menu-item col-4">
        <i class="fa fa-balance-scale icon5"></i>
        <span>质量管理</span>
      </div>
      <div class="menu-item col-4">
        <i class="fa fa-clipboard-list icon6"></i>
        <span>项目管理</span>
      </div>
      <div class="menu-item col-4">
        <i class="fa fa-user icon7"></i>
        <span>人员管理</span>
      </div>
      <div class="menu-item col-4">
        <i class="fa fa-exclamation-triangle icon8"></i>
        <span>应急指挥</span>
      </div>
      <div class="menu-item col-4">
        <i class="fa fa-users icon9"></i>
        <span>市民之声</span>
      </div>
    </div>
    <div class="news">
      <div class="header">
        新闻公告
      </div>
      <div class="small-header">
        更多 <i class="fa fa-angle-double-right"></i>
      </div>
      <div class="list-group">
        <a class="list-group-item list-group-item-action d-flex justify-content-between align-items-center">
          保利在湛江的三大项目开工建设蓄势待发
          <span class="badge badge-primary badge-pill">2019-05-01</span>
        </a>
        <a class="list-group-item list-group-item-action d-flex justify-content-between align-items-center">
          万达在湛江的三大项目开工建设蓄势待发
          <span class="badge badge-primary badge-pill">2019-05-02</span>
        </a>
        <a class="list-group-item list-group-item-action d-flex justify-content-between align-items-center">
          宝钢在湛江的三大项目开工建设蓄势待发
          <span class="badge badge-primary badge-pill">2019-05-03</span>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import IndexCarouserl from './IndexCarousel'
export default {
  name: 'Index',
  components: {
    carouserl: IndexCarouserl
  },
  created: function () {
    this.$emit('setBottomBar', true)
    this.$emit('setReturnBtn', false)
    this.$emit('setMenuBtn', false)
    this.$emit('setTitle', '当前：' + this.$parent.zoneName)
  }
}
</script>

<style lang="scss" scoped>
  @import "../../assets/scss/config";
  .index-menu {
    margin-top: 9px;
    margin-left: 9px;
    margin-right: 9px;
    border-right: $gray-300 solid 1px;
    border-bottom: $gray-300 solid 1px;
    background-color: $gray-100;
    clear: both;
    .menu-item {
      cursor: pointer;
      transition: all .3s ease;
      padding-top: 25px;
      height: 110px;
      border-top: $gray-300 solid 1px;
      border-left: $gray-300 solid 1px;
      text-align: center;
      .fa {
        font-size: 40px;
      }
      span {
        margin-top: 5px;
        display: block;
        font-size: 10px;
        color: $gray-800;
      }
      .icon1 {
        color: $blue;
      }
      .icon2 {
        color: $indigo;
      }
      .icon3 {
        color: $purple;
      }
      .icon4 {
        color: $pink;
      }
      .icon5 {
        color: $red;
      }
      .icon6 {
        color: $orange;
      }
      .icon7 {
        color: $yellow;
      }
      .icon8 {
        color: $green;
      }
      .icon9 {
        color: $cyan;
      }
      &:hover {
        background-color: $gray-200;
      }
    }
  }
  .news {
    .header {
      display: inline-block;
      border-top-right-radius: 5px ;
      border-top-left-radius: 5px ;
      color: white;
      width: 100px;
      background: $blue;
      padding-left: 20px;
      padding-top: 5px;
      padding-bottom: 5px;
    }
    .small-header {
      display: inline-block;
      font-size: 5px;
      color: $gray-800;
      padding-left: 10px;

    }
    margin-top: 9px;
    margin-left: 9px;
    margin-right: 9px;
    .list-group-item {
      transition: all .3s ease;
      border-radius: 0;
    }
  }
</style>



