<template>
<div style="padding-top: 10px">
  <msg-block v-for="(msg, i) in msgBlockInfo" :key="i" :bg-title="msg.title" sm-title1="发布时间" :content1="msg.time" sm-title2="发布者" :content2="msg.sender"></msg-block>
</div>
</template>

<script>
import MsgBlock from '../Utils/MsgBlock'
export default {
  name: 'WFRead',
  components: {
    msgBlock: MsgBlock
  },
  data: function () {
    return {
      msgBlockInfo: [
        {
          id: 1,
          title: '#6钢筋将于明天下午到货进场',
          time: '2019-06-21',
          sender: '原料主管'
        },
        {
          id: 2,
          title: '二号地块地基将于明天早上开挖',
          time: '2019-06-21',
          sender: '规划主管'
        }
      ]
    }
  },
  created: function () {
    this.$emit('setBottomBar', true)
    this.$emit('setReturnBtn', false)
    this.$emit('setMenuBtn', false)
    this.$emit('setTitle', '待阅：' + this.$parent.zoneName)
  }
}
</script>

<style scoped>

</style>



