<template>
  <div class="videoPage">
    <h5 class="text-center">环境噪声监控(dB)</h5>
    <ve-line :data="noiceData"></ve-line>
    <h5 class="text-center">实时噪声监控(dB)</h5>
    <ve-line :data="dynamicData"></ve-line>
    <h5 class="text-center">粉尘指标监控(mg/m^3)</h5>
    <ve-line :data="dustData"></ve-line>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
function random (lower, upper) {
  return Math.floor(Math.random() * (upper - lower)) + lower
}
export default {
  name: 'Status',
  created: function () {
    this.$emit('setBottomBar', false)
    this.$emit('setReturnBtn', true)
    this.$emit('setMenuBtn', false)
    this.$emit('setTitle', '环境：' + this.$parent.zoneName)
    this.dynamicGo()
  },
  data: function () {
    return {
      noiceData: {
        columns: ['时间', '起重机噪声', '切割房噪声', '边界噪声'],
        rows: [
          {'时间': '10:00', '起重机噪声': 80, '切割房噪声': 91, '边界噪声': 65},
          {'时间': '10:10', '起重机噪声': 87, '切割房噪声': 84, '边界噪声': 65},
          {'时间': '10:20', '起重机噪声': 69, '切割房噪声': 50, '边界噪声': 50},
          {'时间': '10:30', '起重机噪声': 91, '切割房噪声': 56, '边界噪声': 55},
          {'时间': '10:40', '起重机噪声': 72, '切割房噪声': 50, '边界噪声': 52},
          {'时间': '10:50', '起重机噪声': 76, '切割房噪声': 98, '边界噪声': 60}
        ]
      },
      dynamicData: {
        columns: ['时间', '起重机噪声', '切割房噪声', '边界噪声'],
        rows: []
      },
      dustData: {
        columns: ['时间', '监测点1', '监测点2', '监测点3'],
        rows: [
          {'时间': '10:00', '监测点1': 3.2, '监测点2': 2.2, '监测点3': 3.5},
          {'时间': '10:10', '监测点1': 4, '监测点2': 1.5, '监测点3': 4.2},
          {'时间': '10:20', '监测点1': 2.2, '监测点2': 3.2, '监测点3': 4.1},
          {'时间': '10:30', '监测点1': 3.1, '监测点2': 2.1, '监测点3': 3},
          {'时间': '10:40', '监测点1': 2.5, '监测点2': 2.4, '监测点3': 3.4},
          {'时间': '10:50', '监测点1': 2.4, '监测点2': 2.6, '监测点3': 3.2}
        ]
      }
    }
  },
  components: {
    'VeLine': VeLine
  },
  methods: {
    dynamicGo: function () {
      let _this = this
      let date = new Date()
      if (_this.dynamicData.rows.length > 20) {
        _this.dynamicData.rows.shift()
      }
      _this.dynamicData.rows.push(
        {'时间': date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds(), '起重机噪声': random(50, 80), '切割房噪声': random(50, 100), '边界噪声': random(55, 68)}
      )
      setTimeout(_this.dynamicGo, 1000)
    }
  }
}
</script>

<style lang="scss" scoped>
  .videoPage {
    padding: 10px 20px 20px 20px;
  }
</style>



