<template>
  <div style="">
    <swiper :options="swiperOption" class="swiper-wrap"  ref="mySwiper" v-if="banner.length!==0">
      <swiper-slide v-for="(item,index) in banner" :key="index" >
        <img :src="item.image" style="width: 100%; height: auto" alt="" />
      </swiper-slide>
      <!-- 常见的小圆点 -->
      <div class="swiper-pagination"  v-for="(item,index) in banner" :key="index" slot="pagination" ></div>
    </swiper>
    <!-- 显示数字 -->
    <!--<div class="number">{{imgIndex}}/{{detailimages.length}}</div>-->
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
require('swiper/dist/css/swiper.css')
export default {
  name: 'IndexCarousel',
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      banner: [
        {
          image: '/static/img/5-2Img-1.png'
        },
        {
          image: '/static/img/5-2Img-2.png'
        }
      ],
      imgIndex: 1,
      swiperOption: {
        // height: 200,
        // 是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
        notNextTick: true,
        // 循环
        loop: true,
        // 设定初始化时slide的索引
        initialSlide: 0,
        // 自动播放
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          /* 触摸滑动后是否继续轮播 */
          disableOnInteraction: false
        },
        // 滑动速度
        speed: 800,
        // 滑动方向
        direction: 'horizontal',
        // 小手掌抓取滑动
        grabCursor: true,
        // 分页器设置
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
          type: 'bullets'
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .swiper-slide {
    width: 100px;
    img {
      height: auto;
    }
  }
</style>



