<template>
<div style="padding-top: 10px">
  <msg-block v-for="(msg, i) in msgBlockInfo" :key="i" :bg-title="msg.title" sm-title1="时间" :content1="msg.time" sm-title2="地点" :content2="msg.zone"></msg-block>
</div>
</template>

<script>
import MsgBlock from '../Utils/MsgBlock'
export default {
  name: 'WFDo',
  components: {
    msgBlock: MsgBlock
  },
  data: function () {
    return {
      msgBlockInfo: [
        {
          id: 1,
          title: '以"科学管理施工现场"为主题的研讨会',
          time: '2019-9-21 9:30-11:30',
          zone: '会议室'
        },
        {
          id: 2,
          title: '施工人员安全教育大会',
          time: '2019-9-22 8:30-10:30',
          zone: '员工大厅'
        },
        {
          id: 3,
          title: '施工任务分组会议',
          time: '2019-9-22 14:30-16:30',
          zone: '会议室'
        }
      ]
    }
  },
  created: function () {
    this.$emit('setBottomBar', true)
    this.$emit('setReturnBtn', false)
    this.$emit('setMenuBtn', false)
    this.$emit('setTitle', '待办：' + this.$parent.zoneName)
  }
}
</script>

<style scoped>

</style>



