<template>
<div class="msg-block">
  <h4 v-if="bgTitle != null" style="text-decoration: none">{{bgTitle}}</h4>
  <div class="col1">
    <small v-if="content1 != null" class="sm-title-1"><b v-if="smTitle1 != null">{{smTitle1}}：</b>{{content1}}</small>
  </div>
  <div class="col2">
    <small v-if="content2 != null" class="sm-title-2"><b v-if="smTitle2 != null">{{smTitle2}}：</b>{{content2}}</small>
  </div>
</div>
</template>

<script>
export default {
  name: 'MsgBlock',
  props: {
    bgTitle: String,
    smTitle1: String,
    content1: String,
    smTitle2: String,
    content2: String
  }
}
</script>

<style lang="scss" scoped>
  @import "../../assets/scss/config";
  .msg-block {
    cursor: pointer;
    color: $gray-800;
    margin: 0 10px 10px 10px;
    background-color: $gray-100;
    border: $gray-300 solid 1px;
    border-radius: 5px;
    padding: 10px;
    transition: background-color ease .3s;
    &:hover {
      text-decoration: none;
      background-color: $white;
    }
  }
  .col1, .col2 {
    display: inline-block;
  }
  .col1 {
    width: calc(70% - 10px);
  }
  .col2 {
    width: calc(30% - 10px);
  }
</style>



