<template>
  <div class="diy-nav-bar fixed-top navbar-dark bg-primary">
    <div class="nav-left" @click="$router.go(-1)">
      <transition name="returnbtn">
        <i class="fa fa-chevron-left" v-show="enableReturnBtn"></i>
      </transition>
    </div>
    <div class="nav-title">{{title}}</div>
    <div class="nav-right">
      <transition name="menubtn">
        <i class="fa fa-ellipsis-h" v-show="enableMenuBtn"></i>
      </transition>
    </div>
  </div>
</template>

<script>

export default {
  name: 'AppHeader',
  props: ['enableReturnBtn', 'enableMenuBtn', 'title']
}
</script>

<style lang="scss" scoped>
  @import "../assets/scss/config";
  .diy-nav-bar {
    padding: 10px;
    padding-top: 13px;
    padding-bottom: 13px;
    max-width: $max-width;
    margin: 0 auto;
    color: white;
    font-size: 20px;
    overflow-x: hidden;
    .nav-left {
      width: 30px;
      margin-left: 10px;
      display: inline-block;
      text-align: left;
      cursor: pointer;
    }
    .nav-title{
      width: calc(100% - 80px - 10px);
      display: inline-block;
      text-align: center;
    }
    .nav-right {
      width: 30px;
      margin-right: 10px;
      display: inline-block;
      text-align: right;
      cursor: pointer;
    }
  }
  .returnbtn-enter-active, .returnbtn-leave-active {
    transition: all .6s ease;
  }
  .returnbtn-enter, .returnbtn-leave-to {
    transform: translateX(-50px);
    opacity: 0;
  }
  .menubtn-enter-active, .menubtn-leave-active {
    transition: all .6s ease;
  }
  .menubtn-enter, .rmenubtn-leave-to {
    transform: translateX(50px);
    opacity: 0;
  }
</style>



