import Vue from 'vue'
import Router from 'vue-router'
import AppLayout from '@/components/AppLayout'
import Index from '@/components/Index/Index'
import WFRead from '@/components/WFRead/WFRead'
import WFDo from '@/components/WFDo/WFDo'
import VideoLive from '@/components/Video/Live'
import EnvStatus from '@/components/Env/Status'
import DeviceStatus from '@/components/Device/Status'
import Zone from '@/components/Zone/Zone'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'AppLayout',
      component: AppLayout,
      children: [
        {
          path: '/',
          name: 'Index',
          component: Index
        },
        {
          path: '/wfread',
          name: 'WFRead',
          component: WFRead
        },
        {
          path: '/wfdo',
          name: 'WFDo',
          component: WFDo
        },
        {
          path: '/video/live',
          name: 'VideoLive',
          component: VideoLive
        },
        {
          path: '/env/status',
          name: 'EnvStatus',
          component: EnvStatus
        },
        {
          path: '/device/status',
          name: 'DeviceStatus',
          component: DeviceStatus
        },
        {
          path: '/zone',
          name: 'Zone',
          component: Zone
        }
      ]
    }
  ]
})



