<template>
<div class="videoPage">
  <h5>监控通道1 - 钢铁加工场</h5>
  <video src="/static/video/live1.mp4" width="100%" muted autoplay loop></video>
  <h5 style="margin-top: 10px">监控通道2 - 外围车棚</h5>
  <video src="/static/video/live2.mp4" width="100%" muted autoplay loop></video>
</div>
</template>

<script>
export default {
  name: 'Live',
  created: function () {
    this.$emit('setBottomBar', false)
    this.$emit('setReturnBtn', true)
    this.$emit('setMenuBtn', false)
    this.$emit('setTitle', '监控：' + this.$parent.zoneName)
  }
}
</script>

<style lang="scss" scoped>
.videoPage {
  padding: 10px 20px 20px 20px;
}
</style>



