<template>
<div>
  <transition name="fade">
    <div class="app-alert" v-show="isShow">
    </div>
  </transition>
  <transition name="box">
    <div class="alert-box" v-show="isShow">
      <div class="col-12">{{title}}</div>
      <div class="col-6 cancel" @click="cancel">{{cancelText}}</div>
      <div class="col-6" @click="confirm">{{confirmText}}</div>
    </div>
  </transition>
</div>
</template>

<script>
export default {
  name: 'Alert',
  data: function () {
    return {
      isShow: false,
      title: '标题',
      cancelText: 'Cancel',
      confirmText: 'Confirm',
      cancelCallback: null,
      confirmCallback: null
    }
  },
  methods: {
    show: function (title, confirmCallback, confirmText, cancelText, cancelCallback) {
      this.title = title
      if (cancelText != null) {
        this.cancelText = cancelText
      } else {
        this.cancelText = '取消'
      }
      if (confirmText != null) {
        this.confirmText = confirmText
      } else {
        this.confirmText = '确认'
      }
      this.cancelCallback = cancelCallback
      this.confirmCallback = confirmCallback
      this.isShow = true
    },
    cancel: function () {
      this.isShow = false
      if (this.cancelCallback != null) {
        this.cancelCallback()
      }
    },
    confirm: function () {
      this.isShow = false
      if (this.confirmCallback != null) {
        this.confirmCallback()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../../assets/scss/config";
.app-alert {
  position: fixed;
  top: 0;
  left: 0;
  background-color: black;
  opacity: 0.5;
  z-index: 99998;
  width: 100vw;
  height: 100vh;
  text-align: center;
}
  .alert-box {
    position: fixed;
    background-color: white;
    width: 300px;
    height: 150px;
    border-radius: 10px;
    top: calc(50vh - 75px);
    left: calc(50vw - 150px);
    opacity: 0.95;
    z-index: 99999;
    text-align: center;
    box-shadow: 0 0 10px 0 #fff;
    .col-12 {
      height: 100px;
      font-size: 18px;
      padding-top: 40px;
      border-bottom: $gray-400 solid 1px;
      color: $gray-800;
    }
    .col-6 {
      height: 50px;
      line-height: 48px;
      width: 50%;
      float: right;
      color: #188fff;
      border-bottom-right-radius: 10px;
      font-size: 16px;
      &.cancel {
        border-right: $gray-400 solid 1px;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 0;
        float: left;
      }
      &:hover {
        cursor: pointer;
        background-color: #eeeeee;
      }
    }
  }
  .box-enter-active, .box-leave-active {
    transition: all .2s ease;
  }
  .box-leave-to{
    transform: scale(0.7, 0.7);
    opacity: 0;
  }

  .box-enter {
    transform: scale(1.3, 1.3);
    opacity: 0;
  }

  .fade-enter-active, .fade-leave-active {
    transition: all .2s ease;
  }
  .fade-leave-to, .fade-enter {
    opacity: 0;
  }
</style>



