<template>
  <div class="videoPage">
    <h5 class="text-center">塔吊倾斜度</h5>
    <ve-line :data="data1"></ve-line>
    <h5 class="text-center">设备健康度（%）</h5>
    <ve-line :data="data2"></ve-line>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
function random (lower, upper) {
  return Math.floor(Math.random() * (upper - lower)) + lower
}
export default {
  name: 'Status',
  created: function () {
    this.$emit('setBottomBar', false)
    this.$emit('setReturnBtn', true)
    this.$emit('setMenuBtn', false)
    this.$emit('setTitle', '设备：' + this.$parent.zoneName)
    this.dynamicGo()
  },
  data: function () {
    return {
      data1: {
        columns: ['时间', '塔吊1', '塔吊2'],
        rows: [
          {'时间': '10:00', '塔吊1': 0.1, '塔吊2': 0.02},
          {'时间': '10:10', '塔吊1': 0.05, '塔吊2': 0.1},
          {'时间': '10:20', '塔吊1': 0.07, '塔吊2': 0.12},
          {'时间': '10:30', '塔吊1': 0.11, '塔吊2': 0.06},
          {'时间': '10:40', '塔吊1': 0.03, '塔吊2': 0.08},
          {'时间': '10:50', '塔吊1': 0.08, '塔吊2': 0.09}
        ]
      },
      data2: {
        columns: ['时间', '升降机', '塔吊'],
        rows: [
          {'时间': '10:00', '升降机': 100, '塔吊': 100},
          {'时间': '10:10', '升降机': 100, '塔吊': 100},
          {'时间': '10:20', '升降机': 100, '塔吊': 100},
          {'时间': '10:30', '升降机': 100, '塔吊': 100},
          {'时间': '10:40', '升降机': 100, '塔吊': 100},
          {'时间': '10:50', '升降机': 100, '塔吊': 100}
        ]
      }
    }
  },
  components: {
    'VeLine': VeLine
  },
  methods: {
    dynamicGo: function () {
      let _this = this
      let date = new Date()
      if (_this.dynamicData.rows.length > 20) {
        _this.dynamicData.rows.shift()
      }
      _this.dynamicData.rows.push(
        {'时间': date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds(), '起重机噪声': random(50, 80), '切割房噪声': random(50, 100), '边界噪声': random(55, 68)}
      )
      setTimeout(_this.dynamicGo, 1000)
    }
  }
}
</script>

<style lang="scss" scoped>
  .videoPage {
    padding: 10px 20px 20px 20px;
  }
</style>



